@import url(https://fonts.googleapis.com/css?family=EB+Garamond:400,400i);
/*
* Colors 
*/
/*
* Breakpoints 
*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body,
button {
  font-family: 'EB Garamond', serif;
  font-size: 1em;
  line-height: 1.6;
  letter-spacing: .0825em; }

a,
button {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none; }
  a.selected::after, a:hover::after,
  button.selected::after,
  button:hover::after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left; }
  a::after,
  button::after {
    content: '';
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 1px;
    background-color: #222;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out; }
  .is-inverted a::after, .is-inverted
  button::after {
    background: #fefefe; }

h2 {
  font-size: 1.25rem;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: .75em; }

button {
  border: 0;
  background-color: transparent;
  cursor: pointer;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

/* 
* Layout 
* 
*/
.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  color: #222;
  background-color: #fefefe;
  transition: color .5s ease-in-out, background-color .5s ease-in-out; }
  @media screen and (min-width: 768px) {
    .wrapper {
      display: flex; } }
  .wrapper.is-inverted {
    color: #fefefe;
    background: #222; }

.main {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 1.75em; }
  @media screen and (min-width: 768px) {
    .main {
      width: 44.4%; } }

/* 
* Utils 
* 
*/
.italic {
  font-style: italic; }

@media screen and (max-width: 767px) {
  .no-mobile {
    display: none; } }

.lazyload,
.lazyloading {
  opacity: 0; }

.lazyloaded {
  opacity: 1;
  transition: opacity .5s; }

/*
* Colors 
*/
/*
* Breakpoints 
*/
@media screen and (min-width: 768px) {
  .gallery {
    position: relative;
    display: block;
    width: 36%;
    height: 100%;
    overflow-y: auto; } }

.gallery-figure {
  margin: 0 0 1em; }
  .gallery-figure:nth-last-child(1) {
    margin-bottom: 0; }
  .gallery-figure > img {
    display: block;
    transition: all .5s ease-in; }
    .is-inverted .gallery-figure > img {
      -webkit-filter: invert(0.8);
              filter: invert(0.8); }

/*
* Colors 
*/
/*
* Breakpoints 
*/
@-webkit-keyframes bouncing-loader {
  to {
    opacity: 0.1;
    -webkit-transform: translate3d(0, -1rem, 0);
            transform: translate3d(0, -1rem, 0); } }
@keyframes bouncing-loader {
  to {
    opacity: 0.1;
    -webkit-transform: translate3d(0, -1rem, 0);
            transform: translate3d(0, -1rem, 0); } }

.bouncing-loader {
  display: flex;
  justify-content: center; }
  .bouncing-loader > div {
    width: .75rem;
    height: .75rem;
    margin: 3rem 0.4rem;
    background: #222;
    border-radius: 50%;
    -webkit-animation: bouncing-loader 0.3s infinite alternate;
            animation: bouncing-loader 0.3s infinite alternate; }
    .bouncing-loader > div:nth-child(2) {
      -webkit-animation-delay: 0.1s;
              animation-delay: 0.1s; }
    .bouncing-loader > div:nth-child(3) {
      -webkit-animation-delay: 0.2s;
              animation-delay: 0.2s; }

/*
* Colors 
*/
/*
* Breakpoints 
*/
.nav {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background-color: #222;
  color: #fefefe;
  padding: 1.75em;
  transition: color .3s ease-in, background-color .3s ease-in; }
  @media screen and (min-width: 768px) {
    .nav {
      background-color: transparent;
      color: #222; }
      .is-inverted .nav {
        color: #fefefe; } }
  .nav a::after {
    background-color: #fefefe; }
  @media screen and (min-width: 768px) {
    .nav a::after {
      background-color: #222; } }

.name {
  margin: 0;
  font-size: 1em;
  font-weight: 400; }

